import React, { useCallback, useEffect, useRef, useState } from "react";
import ReactCanvasConfetti from "react-canvas-confetti";
import "./App.css"
import WheelOne from "./WheelOne";
import WheelTwo from "./WheelTwo";
import "./BackgroundAnimation";
import { Checkbox, useCheckboxState } from "pretty-checkbox-react";
import 'pretty-checkbox';
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import ReactTimerStopwatch from 'react-stopwatch-timer';

import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import AvTimerIcon from '@mui/icons-material/AvTimer';
import { pink, common } from '@mui/material/colors';
import InputLabel from '@mui/material/InputLabel';

const MySwal = withReactContent(Swal)

const canvasStyles = {
  position: "fixed",
  pointerEvents: "none",
  width: "100%",
  height: "100%",
  top: 0,
  left: 0
};
function randomInRange(min, max) {
  return Math.random() * (max - min) + min;
}
function getAnimationSettings(originXA, originXB) {
  return {
    startVelocity: 30,
    spread: 360,
    ticks: 60,
    zIndex: 0,
    particleCount: 150,
    origin: {
      x: randomInRange(originXA, originXB),
      y: Math.random() - 0.2
    }
  };
}

export default () => {
  const initialMsg = 'Please spin the Left wheel';
  const [enableWheelOne, setenableWheelOne] = useState(true);
  const [enableWheelTwo, setenableWheelTwo] = useState(false);
  const [msg, setmsg] = useState(initialMsg);
  const [wheelOnePick, setwheelOnePick] = useState('');

  const [timerSec, setTimerSec] = React.useState(30);
  const handleChangeTimerVal = (event) => {
    setTimerSec(event.target.value);
  }
  const refAnimationInstance = useRef(null);
  const [intervalId, setIntervalId] = useState();
  const getInstance = useCallback((instance) => {
    refAnimationInstance.current = instance;
  }, []);
  const nextTickAnimation = useCallback(() => {
    if (refAnimationInstance.current) {
      refAnimationInstance.current(getAnimationSettings(0.1, 0.3));
      refAnimationInstance.current(getAnimationSettings(0.7, 0.9));
    }
  }, []);
  const startAnimation = useCallback(() => {
    if (!intervalId) {
      setIntervalId(setInterval(nextTickAnimation, 600));
    }
  }, [intervalId, nextTickAnimation]);

  const pauseAnimation = useCallback(() => {
    clearInterval(intervalId);
    setIntervalId(null);
  }, [intervalId]);

  useEffect(() => {
    return () => {
      clearInterval(intervalId);
    };
  }, [intervalId]);

  //Step 2 
  const afterWheelOneRun = (wheelOnePickResp) => {
    setenableWheelOne(false);
    setenableWheelTwo(true);
    setwheelOnePick(wheelOnePickResp)
    setmsg(`${wheelOnePickResp}, Please spin the Right wheel`)
  }

  //Step 3 (initial)
  const afterWheelTwoRun = (wheelTwoPick) => {
    setenableWheelOne(true);
    setenableWheelTwo(false);
    const time = timerSec;
    const fromTime = new Date(0, 0, 0, 0, 0, time, 0);
    setmsg(initialMsg);
    startAnimation();
    setTimeout(() => {
      pauseAnimation();
    }, 2000);
    MySwal.fire({
      title: <><div align="center"><ReactTimerStopwatch isOn={true} className="react-stopwatch-timer__table" watchType="timer"
        displayCircle={true} color="gray" hintColor="red" fromTime={fromTime} />
        <ReactCanvasConfetti refConfetti={getInstance} style={canvasStyles} />
        </div>
        {wheelOnePick} : {wheelTwoPick}
      </>,
      timer: time * 1000,
      // didOpen: () => {
      //   // `MySwal` is a subclass of `Swal` with all the same instance & static methods
      //   Swal.showLoading(Swal.getDenyButton());
      // },
    });
  }

  const checkbox = useCheckboxState({ state: true });

  const [mustSpinSecWheel, setMustSpinSecWheel] = useState(false);
  const [prizeNumberSecWheel, setPrizeNumberSecWheel] = useState(0);

  // TODO - Write logic for enable and disable one wheel after another


  return (
    <>
      <div align="center">
        <h1 align="center">Rapid Questions Roulette</h1>
        <h2 align="center">{msg}</h2>
        <hr />

        <div className="wheel-wrapper row">
          <WheelOne enableSpin={enableWheelOne} removeOnPick={checkbox.state} spinCompleted={afterWheelOneRun}></WheelOne>
          <WheelTwo enableSpin={enableWheelTwo} removeOnPick={checkbox.state} spinCompleted={afterWheelTwoRun}></WheelTwo>
        </div>
        <div className="row">
          <div className="input-cols">

            <Checkbox shape="round" animation="jelly" color="danger" value="" {...checkbox} icon={<i className="mdi mdi mdi-minus-circle" />}>Remove on pick</Checkbox>
          </div>
          <div className="input-cols">
            <Box sx={{ display: 'inline-flex' }}>
              <AvTimerIcon sx={{ color: pink[500], border: common, alignSelf: 'center' }} />
              <TextField  value={timerSec} onChange={handleChangeTimerVal} type="number" id="input-with-sx" sx={{ mt: '0%', padding:0, width: '3ch' }} variant="standard" />
              <InputLabel  sx={{ mt: '5%'}} htmlFor="input-with-sx">Seconds</InputLabel>
            </Box>
          </div>
        </div>
        <hr />

      </div>
    </>
  );
};
